import React from 'react'

export const SandwitchButton = ({ click }) => {
    return (
        <button onClick={click} className='rounded-sm flex items-center px-3 py-2 border text-teal-800 border-teal-700 hover:text-black hover:border-teal-900'>
            <svg className="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" /></svg>
        </button>
    )
}
